.bio__container {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100vh;
    min-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .bio__content__container {
        width: 60vw;
        height: 600px;
        padding: 20px;
        color: white;

        .bio__picture {
            width: 300px;
            height: 300px;
            margin: 0 auto;
            background-image: url('../../assets/piotrek.jpg');
            background-size: cover;
            background-position: center;

            
            @media(max-width: 600px) {
                width: calc(100% - 20px);
                //width: 100%;
                height: 250px;
            }

            @media(max-width: 600px) {
                //width: calc(100% - 20px);
                //width: 100%;
                height: 220px;
            }
            

