            //     height: 270px;
            //     width: 270px;
            // }

            // @media screen and (min-device-width: 551px) and (max-device-width: 650px) {
            //     width: 250px;
            //     height: 250px;
            // }

            // @media screen and (max-width: 550px) {
            //     width: 230px;
            //     height: 230px;
            // }
        }

        .bio__title {
            margin-top: 40px;
            text-align: center;
            text-transform: uppercase;
            font-size: 22px;
            letter-spacing: 3px;

            @media(max-width: 374px) {
                margin-top: 10px;
                font-size: 18px;
            }

            // @media screen and (max-width: 700px) {
            //     margin-top: 20px;
            //     font-size: 20px;
            // }

            // @media screen and (max-width: 600px) {
            //     margin-top: 10px;
            //     font-size: 18px;
            // }
        }

        .bio__text__holder {
            width: 80%;
            margin: 10px auto;

            @media(max-width: 375px) {
                width: 90%;
                margin-top: 5px auto;
            }
            
            p {
                font-size: 17px;
                text-align: center;
                letter-spacing: 5px;
                line-height: 30px;

                @media(max-width: 375px) {
                    font-size: 15px;
                    line-height: 20px;
